import React from 'react'
import './SubGallery.scss';
// import simpleParallax from 'simple-parallax-js'

import img1 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC01.jpg';
import img2 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC02.jpg';
import img3 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC03.jpg';
import img4 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC04.jpg';
import img5 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC05.jpg';
import img6 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC06.jpg';
import img7 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC07.jpg';
import img8 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC08.jpg';
import img9 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC09.jpg';
import img10 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC10.jpg';

import img1S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_01.jpg';
import img2S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_02.jpg';
import img3S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_03.jpg';
import img4S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_04.jpg';
import img5S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_05.jpg';
import img6S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_06.jpg';
import img7S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_07.jpg';
import img8S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_08.jpg';
import img9S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_09.jpg';
import img10S from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400/DSC_10.jpg';

const imgSet1 = [
    { id: 0, path: img1, pathS: img1S, },
    { id: 1, path: img2, pathS: img2S, },
    { id: 2, path: img3, pathS: img3S, },
    { id: 3, path: img4, pathS: img4S, },
    { id: 4, path: img5, pathS: img5S, },
    { id: 5, path: img6, pathS: img6S, },
    { id: 6, path: img7, pathS: img7S, },
    { id: 7, path: img8, pathS: img8S, },
    { id: 8, path: img9, pathS: img9S, },
    { id: 9, path: img10, pathS: img10S, },
]

const photos = imgSet1.map(item => {
    return (
        <div className={`SubGallery__photo img${item.id}`} key={item.id}>
            <picture>
                <source media="(min-width: 461px)" srcSet={item.path} />
                <img src={item.pathS} className="d-block w-100" alt="..." />
            </picture>
        </div >
    )
})

class Swierklaniec extends React.Component {

    handleItemAppear = () => {
        for (let i = 0; i < imgSet1.length; i++) {
            const scrollValue = window.scrollY
            const item = document.querySelector(`.img${i}`)
            const itemTop = item.offsetTop
            const itemHeight = item.offsetHeight
            const windowHeight = window.innerHeight
            const itemMiddle = itemTop + itemHeight / 2 - windowHeight

            if (scrollValue > itemMiddle && scrollValue < itemMiddle + 1100) {
                item.classList.add(`SubGallery__photo--visible`)
            }
            else if (scrollValue > itemMiddle) {
                item.classList.remove(`SubGallery__photo--visible`)
            }
            else {
                item.classList.remove(`SubGallery__photo--visible`)
            }
        }
    }

    componentDidMount = () => {
        window.addEventListener('scroll', this.handleItemAppear)
    }

    componentWillUnmount = () => {
        window.removeEventListener('scroll', this.handleItemAppear)
    }

    render() {
        return (
            <div className="SubGallery">
                <div className="SubGallery__photos">
                    {photos}
                </div>
            </div>
        )
    }
}

export default Swierklaniec
