import React from 'react'
import './Gallery.scss';
import { Link } from 'react-router-dom';
// import simpleParallax from 'simple-parallax-js'

import img1 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 1080x720/DSC02.jpg';
import img2 from '../img/Rodzinne i dzieciÄ™ce/2018.06.16- Åšwierklaniec 600x400 etykieta/DSC0.jpg';
import img3 from '../img/Rodzinne i dzieciÄ™ce/2018.08.11- Bielkowo- Nat ogrÃ³d 600x400-w2 etykieta/DSC0.jpg';
import img4 from '../img/Rodzinne i dzieciÄ™ce/2020.02.16- Natal- 600x400 etykieta/DSC0.jpg';

const imgSet1 = [
    { path: img2, name: 'Swierklaniec', },
    { path: img3, name: 'Bielkowo', },
    { path: img4, name: 'Natal', },
]

const photos = imgSet1.map(item => {
    return (
        <div className="Gallery__photo" key={item.path}>
            <Link to={`/${item.name}`}>
                <picture>
                    <source media="(min-width: 461px)" srcSet={item.path} />
                    <img src={item.path} className="d-block w-100" alt="..." />
                </picture>
            </Link>
        </div >
    )
})

class Family extends React.Component {

    handleParallax = () => {
        const item = document.querySelector('.Gallery__mainPhoto')
        const scrolled = window.pageYOffset
        const rate = scrolled * .6
        item.style.transform = `translate(0px, ${rate}px)`
    }

    componentDidMount = () => {
        //     const image = document.getElementsByClassName('thumbnail');
        //     new simpleParallax(image, {
        //         scale: 1.5,
        //         delay: 1,
        //     });

        window.addEventListener('scroll', this.handleParallax)
    }

    componentWillUnmount = () => {
        window.removeEventListener('scroll', this.handleParallax)
    }

    render() {
        return (
            <div className="Gallery">
                <picture>
                    <source media="(min-width: 461px)" srcSet={img1} />
                    <img src={img1} className="Gallery__mainPhoto thumbnail d-block w-100" alt="..." />
                </picture>
                <div className="Gallery__photos">
                    <div className="Gallery__photosFlexContainer">
                        {photos}
                    </div>
                </div>
            </div>
        )
    }
}

export default Family
