import React from 'react'
import './Main.scss';
// import './MainSlider.scss';

import img2 from '../img/aircraft.jpg';
import img2s from '../img/aircraftS.jpg';
import img4 from '../img/flower.jpg';
import img4s from '../img/flowerS.jpg';
import img6 from '../img/glacier.jpg';
import img6s from '../img/glacierS.jpg';
import img7 from '../img/switzerland.jpg';

const imgSet1 = [
    { path: img4, pathS: img4s, },
    { path: img2, pathS: img2s, },
    { path: img6, pathS: img6s, },
    { path: img7, pathS: img7, },
]

const photos = imgSet1.map(item => {
    return (
        <div className="Main__photo" key={item.path}>
            <picture>
                <source media="(min-width: 461px)" srcSet={item.path} />
                <img src={item.pathS} className="d-block w-100" alt="..." />
            </picture>
        </div>
    )
})

class Main extends React.Component {

    handleParallax = () => {
        const item = document.querySelector('.MainSlider__carousel')
        const scrolled = window.pageYOffset
        const rate = scrolled * .6
        item.style.transform = `translate(0px, ${rate}px)`
    }

    handleParallaxBelow = () => {
        const item = document.querySelector('.Main__photos')
        const scrolled = window.pageYOffset
        const rate = scrolled * .1
        item.style.transform = `translate(0px, ${rate}px)`
    }

    componentDidMount = () => {
        window.addEventListener('scroll', this.handleParallax)
        window.addEventListener('scroll', this.handleParallaxBelow)
    }

    componentWillUnmount = () => {
        window.removeEventListener('scroll', this.handleParallax)
        window.removeEventListener('scroll', this.handleParallaxBelow)
    }

    render() {
        return (
            <div className="Main">
                <div className="Main__photos">
                    <div className="Main__photosFlexContainer">
                        {photos}
                    </div>
                </div>
            </div>
        );
    }
}

export default Main;
