import React from 'react';
import './App.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter, Route } from 'react-router-dom';
import ScrollToTop from 'react-router-scroll-top';

import Hamburger from './Components/Hamburger';
import Menu from './Components/Menu';
import Logo from './Components/Logo';

import MainPage from './Components/MainPage'
import About from './Components/About'
import Offer from './Components/Offer'
import Dogs from './Components/Dogs'
import Family from './Components/Family'
import Portraits from './Components/Portraits'
import Blog from './Components/Blog'
import References from './Components/References'
import Contact from './Components/Contact'

import Swierklaniec from './Components/Swierklaniec'
import Bielkowo from './Components/Bielkowo'
import Natal from './Components/Natal'

import Footer from './Components/Footer'

class App extends React.Component {

	state = {
		menuActive: false,
		// footerPortfolioActive: false,
	}

	handleMenu = () => {
		this.setState({
			menuActive: !this.state.menuActive,
		});
	}

	handleArrow = () => {
		let pageHeight = window.innerHeight;
		window.scrollBy(0, pageHeight);
	}

	componentDidMount = () => {
		window.addEventListener('scroll', () => {
			// document.querySelector('.Menu').classList.add("Menu--afterScroll")
			if (window.scrollY) {
				document.querySelector('.Menu').classList.add("Menu--afterScroll")
			} else {
				document.querySelector('.Menu').classList.remove("Menu--afterScroll")
			}
		});
	};

	render() {
		return (
			<div className="App">
				<BrowserRouter basename={process.env.PUBLIC_URL}>
					<Hamburger
						menuActive={this.state.menuActive}
						handleMenu={this.handleMenu}
					/>
					<Menu
						menuActive={this.state.menuActive}
						handleMenu={this.handleMenu}
					/>
					<Logo />
					<ScrollToTop>
						<Route exact path="/"
							render={(props) => <MainPage {...props}
								handleArrow={this.handleArrow} />}
						/>
						<Route path="/about" component={About} />
						<Route path="/offer" component={Offer} />
						<Route path="/dogs" component={Dogs} />
						<Route path="/family" component={Family} />
						<Route path="/portraits" component={Portraits} />
						<Route path="/blog" component={Blog} />
						<Route path="/references" component={References} />
						<Route path="/contact" component={Contact} />

						<Route path="/Swierklaniec" component={Swierklaniec} />
						<Route path="/Bielkowo" component={Bielkowo} />
						<Route path="/Natal" component={Natal} />
					</ScrollToTop>
					<Footer
					// footerPortfolioActive={this.state.footerPortfolioActive}
					// handleFooterPortfolio={this.handleFooterPortfolio}
					/>
				</BrowserRouter>
			</div>
		);
	}
}

export default App;
