import React from 'react'
import './Gallery.scss';
import { Link } from 'react-router-dom';
import simpleParallax from 'simple-parallax-js'

import img1 from '../img/aircraft.jpg';

const imgSet1 = [
    // { path: img2, pathS: img2s, name: 'Ula' },
    // { path: img4, pathS: img4s, name: 'Ola' },
    // { path: img6, pathS: img6s, name: 'Ala' },
    // { path: img7, pathS: img7, name: 'Ala' },
]

const photos = imgSet1.map(item => {
    return (
        <div className="Gallery__photo" key={item.path}>
            <Link to={`/${item.name}`}>
                <picture>
                    <source media="(min-width: 461px)" srcSet={item.path} />
                    <img src={item.pathS} className="d-block w-100" alt="..." />
                </picture>
            </Link>
        </div >
    )
})

class Dogs extends React.Component {

    componentDidMount = () => {
        const image = document.getElementsByClassName('thumbnail');
        new simpleParallax(image, {
            scale: 1.7,
            delay: 1,
        });
    }

    render() {
        return (
            <div className="Gallery">
                {/* <div className="Dogs__mainPhoto"></div> */}
                {/* <img className="Dogs__mainPhoto thumbnail" src={img1} alt="..." /> */}
                <picture>
                    <source media="(min-width: 461px)" srcSet={img1} />
                    <img src={img1} className="Gallery__mainPhoto thumbnail d-block w-100" alt="..." />
                </picture>
                <div className="Gallery__photos">
                    {photos}
                </div>
            </div>
        )
    }
}

export default Dogs
